.listing_card,
.listing_card_compact {
  width: 100%;
  border-radius: 0;
  border:none;
  color: black;
  display: flex;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    'Helvetica Neue', Arial, 'Noto Sans', sans-serif, 'Apple Color Emoji',
    'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
}
.listing_card.hover,
.listing_card:hover,
.listing_card_compact.hover,
.listing_card_compact:hover {
  color: black;
  text-decoration: none;
  box-shadow: 0 0 0 0.3rem rgba(28, 193, 104, 0.25);
}
.listing_card {
  width: 100%;
  margin-bottom: 20px;
}
.listing_card_image_container {
  height: 285px;
  position: relative;
}

.listing_card_image {
  height: 285px;
  -o-object-fit: cover;
  object-fit: cover;
  width: 100%;
}
.listing_card_tags {
  position: absolute;
  top: 0;
}
.listing_card_bottom_tags {
  bottom: 72px;
  position: absolute;
}
.listing_card_tag {
  background-color: white;
  color: #333;
  font-size: 12px;
  line-height: 12px;
  padding: 6px 22px;
  text-align: center;
  text-transform: uppercase;
  opacity: 0.85;
}
.listing_card_tag.recent {
  background-color: rgba(120, 191, 238, 0.75);
  color: white;
}
.listing_card_tag.exclusive {
  background-color: rgba(114, 42, 160, 0.75);
  color: white;
}
.listing_card_tag.new_condition {
  background-color: rgba(114, 42, 160, 0.75);
  color: white;
}
.listing_card_tag.super_featured {
  background-color: #ef6d6d;
  color: white;
}
.listing_card_tag.featured {
  background-color: rgba(28, 193, 104, 0.75);
  color: white;
}
.listing_card_tag.reference {
  background-color: #722aa0;
  color: white;
}
.listing_card_image_hidden {
  align-items: center;
  background-color: rgba(255, 255, 255, 0.66);
  display: flex;
  flex-direction: column;
  height: 203px;
  justify-content: flex-end;
  position: absolute;
  top: 0;
  width: 100%;
}
.listing_card_image_hidden_text {
  font-size: 18px;
  font-weight: bolder;
  margin-bottom: 10px;
}
.listing_card_location_names_and_prices,
.listing_card_map {
  bottom: 0;
  display: flex;
  position: absolute;
  width: 100%;
  z-index: 1;
}
.listing_card_location_names_and_prices {
  background-color: rgba(255, 255, 255, 0.6);
  display: inline-block;
  padding: 5px 5px 5px 12px;
  height: 72px;
  /* width: calc(100% - 88px); */
}
.listing_card_location_names {
  font-size: 10px;
  font-weight: 500;
  text-transform: uppercase;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 12px;
}
.listing_card_map {
  right: 0;
  background: #e8e8e8;
  height: 72px;
  width: 88px;
}
.listing_card_title_and_agency {
  height: 62px;
  padding: 12px 12px 8px;
  display: flex;
}
.listing_card_title {
  color: #334c97;
  font-weight: 500;
  flex: 0 0 67%;
  width: 67%;
  white-space: normal;
  overflow: hidden;
}
.listing_card_title.listing_card_title_full_width {
  flex: 0 0 100%;
  width: 100%;
}
.listing_card_agency {
  flex: 0 0 33%;
  height: 38px;
  -o-object-fit: contain;
  object-fit: contain;
  overflow: hidden;
  padding-left: 5px;
  width: 33%;
}
.listing_card_details {
  border-top: 1px solid #e3e3e3;
  color: #474747;
  display: flex;
  font-weight: 400;
  justify-content: space-between;
  padding: 13px 12px;
}
.listing_card_details {
  border-left: 1px solid #666;
}
.listing_card_price {
  display: flex;
  font-weight: bold;
  line-height: 34px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.listing_card_price {
  font-size: 24px;
  margin-top: 5px;
  margin-right: 3px;
}
.listing_card_price_per_square_meter {
  font-size: 11px;
  line-height: 11px;
  font-weight: 500;
  font-size: 13px;
  line-height: 13px;
}
.listing_card_location_names {
  padding-bottom: 0;
}
.listing_card_prices {
  display: block;
}
.listing_card_prices .listing_card_price {
  font-size: 20px;
  line-height: 1;
  margin-bottom: 3px;
}
.listing_card_prices .listing_card_price_per_square_meter {
  margin-top: 0;
}

.listing_card_title {
  font-size: 17px;
  line-height: 20px;
}
.listing_card_details {
  font-size: 14px;
}


